<template>
    <div class="report-container">
        <!-- 标题 -->
        <div class="report-title">
            <span>{{ reportTitle }}</span>
        </div>
        <!-- 评估报告介绍文字 -->
        <div v-if="isPushRes" class="report-content">
            <div class="report-intro">
                <p style="font-weight: bolder; margin-bottom: 5px;">尊敬的{{ username }}：</p>
                <p style="text-indent: 2em; margin-bottom: 5px;">您好！</p>
                <p style="text-indent: 2em">{{ assessReport }}</p>
            </div>
            <!-- 得分结果文字 -->
            <div class="report-result">
                <p>您本次评估得分为<span>{{ userScore.personalScore }}</span>，公司<span>{{ userType[0] }}</span>人员平均得分为<span>{{
                    userScore.otherScore1 }}</span>，公司<span>{{ userType[1] }}</span>群体平均得分为<span>{{
                            userScore.otherScore2 }}</span></p>
            </div>
            <!-- 得分雷达图 -->
            <div class="radar-chart">
                <a-spin v-if="loading && isPushRes" tip="结果查询中..." size="large" class="spin-container" />
                <div class="chart-container">
                    <!-- 业务类型-得分雷达图 -->
                    <div ref="chartRef1" class="radar-chart1"></div>
                    <!-- LXYZ类型-得分雷达图 -->
                    <div ref="chartRef2" class="radar-chart2"></div>
                </div>
            </div>
        </div>
        <div v-else class="no-permission">
            <a-empty description="还未发布本轮评估结果0~0" />
        </div>
    </div>
</template>

<script setup>
import { onMounted, ref, reactive, nextTick } from "vue";
import * as echarts from 'echarts';
import { getHeadnoteApi } from "@/api/headnote";
import { getReportDataApi } from "@/api/growthReport";
import { getStepTimeApi } from "@/api/scoreTimeSet"

// 获取当前评估环节
const isPushRes = ref(false);
const currentSession = ref()
// 获得当前评估进度
const getStepTime = () => {
    getStepTimeApi()
        .then(res => {
            if (res.data.code === 200) {
                for (let i = 0; i <= res.data.data.evaluateStep.length - 1; i++) {
                    if (res.data.data.evaluateStep[i].enable === 1) {
                        currentSession.value = i + 1 + '';
                        if (currentSession.value === '4') {
                            isPushRes.value = true
                        }
                    }
                }
            } else {
                console.log('获取失败！' + res.data.msg);
            }
        })
        .catch(err => {
            console.log('获取失败！', err);
        });
};

// 引用必要的 DOM 元素
const chartRef1 = ref(null);  // 引用得分雷达图1的 DOM 容器
const chartRef2 = ref(null);  // 引用得分雷达图2的 DOM 容器

// 存储用户信息
const username = ref(localStorage.getItem('username'));
const assessReport = ref('');
const reportTitle = ref('')
const userScore = reactive({
    personalScore: '',
    otherScore1: '',
    otherScore2: ''
});
const userType = ref(['', '']);

// 存储不同的得分列表
const selfList = ref([0, 0, 0, 0, 0, 0, 0, 0, 0]);
const lxyzList = ref([0, 0, 0, 0, 0, 0, 0, 0, 0]);
const businessList = ref([0, 0, 0, 0, 0, 0, 0, 0, 0]);

// 获取卷首信息
const getHeadnote = () => {
    getHeadnoteApi().then(res => {
        if (res.data.code === 200) {
            assessReport.value = res.data.data.openingRemarks;
            reportTitle.value = res.data.data.title+' 个人成长报告';
        } else {
            console.log('获取失败！', res.data.msg);
        }
    }).catch(err => {
        console.log('获取失败！', err);
    });
};

// 获取成长报告数据
const lxyzMax = ref()
const businessMax = ref()
const selfMax = ref()
const getReportData = async () => {
    try {
        const res = await getReportDataApi();
        if (res.data.code === 200) {
            userType.value[0] = res.data.data.business;
            userType.value[1] = res.data.data.lxyz;

            userScore.personalScore = res.data.data.totalScore;
            userScore.otherScore1 = res.data.data.businessTotalScore;
            userScore.otherScore2 = res.data.data.lxyzTotalScore;

            selfList.value = res.data.data.selfList;
            lxyzList.value = res.data.data.lxyzList;
            businessList.value = res.data.data.businessList;

            lxyzMax.value = Math.max(...res.data.data.lxyzList);
            businessMax.value = Math.max(...res.data.data.businessList);
            selfMax.value = Math.max(...res.data.data.selfList);

            if(selfMax.value>lxyzMax.value){
                lxyzMax.value = selfMax.value
            }

            if(selfMax.value>businessMax.value){
                businessMax.value = selfMax.value
            }


            // 使用 nextTick 确保 Vue 数据更新后再初始化图表
            nextTick(() => {
                initChart1();
                initChart2();
            });
        } else {
            // console.log('获取失败！', res.data.msg);
        }
    } catch (err) {
        console.log('获取失败！', err);
    }
};

// 初始化得分雷达图1
const initChart1 = () => {
    // 获取图表容器
    const chartContainer1 = document.querySelector('.radar-chart1');
    // 创建图表实例
    const myChart1 = echarts.init(chartContainer1);
    // 定义得分雷达图1的配置选项
    const radarOptions1 = {
        title: {
            text: '个人得分 vs ' + userType.value[0] + '类平均得分',
            left: 'center',
            textStyle: {
                color: '#333',
                fontSize: 15,
            },
        },
        legend: {
            data: ['个人得分', userType.value[0] + '类平均得分'],
            bottom: 10,
            textStyle: {
                color: '#333',
                fontSize: 12,
            },
        },
        radar: {
            indicator: [
                { name: '开放进取 (O)', max: businessMax.value },
                { name: '打胜仗 (W)', max: businessMax.value },
                { name: '创新求实 (I)', max: businessMax.value },
                { name: '信任互助 (T)', max: businessMax.value },
                { name: '尊重平等 (R)', max: businessMax.value },
                { name: '业精于勤 (D)', max: businessMax.value },
                { name: '使命必达 (M)', max: businessMax.value },
                { name: '五湖四海 (F)', max: businessMax.value },
                { name: '坚持自省 (I)', max: businessMax.value },
            ],
            radius: '60%',
        },
        tooltip: {},
        series: [
            {
                name: '个人得分 vs ' + userType.value[0] + '类平均得分',
                type: 'radar',
                data: [
                    {
                        value: selfList.value,
                        name: '个人得分',
                        itemStyle: { color: '#5087EC' }, // 蓝色
                        lineStyle: { color: '#5087EC' },
                        areaStyle: { opacity: 0.1, color: '#5087EC' },
                        label: { show: true, color: '#5087EC', formatter: (params) => params.value }, // 显示数值
                    },
                    {
                        value: businessList.value,
                        name: userType.value[0] + '类平均得分',
                        itemStyle: { color: '#68BBC4' }, // 绿色
                        lineStyle: { color: '#68BBC4' },
                        areaStyle: { opacity: 0.1, color: '#68BBC4' },
                        label: { show: false }, // 不显示数值
                    },
                ],
            },
        ],
    };
    // 设置图表的配置
    myChart1.setOption(radarOptions1);
    // 添加 resize 事件监听器，确保窗口大小变化时图表自动调整
    const resizeHandler = () => {
        myChart1.resize();  // 调整图表大小
    };
    // 绑定 resize 事件
    window.addEventListener('resize', resizeHandler);
};


// 初始化得分雷达图2
const initChart2 = () => {
    // 获取图表容器
    const chartContainer2 = document.querySelector('.radar-chart2');
    // 创建图表实例
    const myChart2 = echarts.init(chartContainer2);
    // 定义得分雷达图2的配置选项
    const radarOptions2 = {
        title: {
            text: '个人得分 vs ' + userType.value[1] + '类平均得分',
            left: 'center',
            textStyle: {
                color: '#333',
                fontSize: 15,
            },
        },
        tooltip: {},
        legend: {
            data: ['个人得分', userType.value[1] + '类平均得分'],
            bottom: 10,
            textStyle: {
                color: '#333',
                fontSize: 12,
            },
        },
        radar: {
            indicator: [
                { name: '开放进取 (O)', max: lxyzMax.value },
                { name: '打胜仗 (W)', max: lxyzMax.value },
                { name: '创新求实 (I)', max: lxyzMax.value },
                { name: '信任互助 (T)', max: lxyzMax.value },
                { name: '尊重平等 (R)', max: lxyzMax.value },
                { name: '业精于勤 (D)', max: lxyzMax.value },
                { name: '使命必达 (M)', max: lxyzMax.value },
                { name: '五湖四海 (F)', max: lxyzMax.value },
                { name: '坚持自省 (I)', max: lxyzMax.value },
            ],
            radius: '60%',
        },
        series: [
            {
                name: '个人得分 vs ' + userType.value[1] + '类平均得分',
                type: 'radar',
                data: [
                    {
                        value: selfList.value,
                        name: '个人得分',
                        itemStyle: { color: '#5087EC' },
                        lineStyle: { color: '#5087EC' },
                        areaStyle: { opacity: 0.1, color: '#5087EC' },
                        label: { show: true, color: '#5087EC', formatter: (params) => params.value },
                    },
                    {
                        value: lxyzList.value,
                        name: userType.value[1] + '类平均得分',
                        itemStyle: { color: '#68BBC4' },
                        lineStyle: { color: '#68BBC4' },
                        areaStyle: { opacity: 0.1, color: '#68BBC4' },
                        label: { show: false },
                    },
                ],
            },
        ],
    };
    // 设置图表的配置
    myChart2.setOption(radarOptions2);
    // 添加 resize 事件监听器，确保窗口大小变化时图表自动调整
    const resizeHandler = () => {
        myChart2.resize();  // 调整图表大小
    };
    // 绑定 resize 事件
    window.addEventListener('resize', resizeHandler);
};

const loading = ref(true)
onMounted(async () => {
    // 等待 getStepTime 和 getHeadnote 执行完毕
    await Promise.all([getStepTime(), getHeadnote()]);

    // 等待 0.5 秒钟
    await new Promise(resolve => setTimeout(resolve, 500));

    // 0.5秒后执行if逻辑
    if (isPushRes) {
        getReportData();  // 获取数据后会自动初始化图表
        loading.value = false
    }
});
</script>
