    padding: 5px 10px;
    overflow-y: auto;
}

.report-title {
    // border: 1px solid red;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;

    span {
        font-size: 16px;
        font-weight: bold;
        border-bottom: 2px solid #3a8ee6;
        color: #3a8ee6;
        margin: auto 0;
        padding-bottom: 5px;
    }
}

