import axios from "axios";

//获取成长报告数据
const getReportDataApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/panel',
        headers:{
            "Content-Type": "application/json",
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

export{
    getReportDataApi,
}