<!-- 评估时间轴展示 -->
<template>
    <div class="timeline-container">
        <span class="timeline-title">{{ reportTitle }}-评估流程时间节点</span>
        <div class="timeline-content">
            <a-steps v-model:current="current" :items="items" :disabled="true">
                <template #progressDot="{ index, status, prefixCls }">
                    <a-popover>
                        <span :class="`${prefixCls}-icon-dot`"></span>
                    </a-popover>
                </template>
            </a-steps>
        </div>
    </div>
</template>

<script setup>
import { onMounted, ref } from "vue"
import { getStepTimeApi } from "@/api/scoreTimeSet"
import { getHeadnoteApi } from "@/api/headnote";

const current = ref(2);
const items = ref([
    {
        title: '建立评估矩阵关系',
        description: '07.01-07.05',
    }
])

// 获取评估节点
const getStepTime = () => {
    getStepTimeApi()
        .then(res => {
            if (res.data.code === 200) {
                items.value = [];
                for (let i = 0; i <= res.data.data.evaluateStep.length - 1; i++) {
                    const startDate = res.data.data.evaluateStep[i].startDate.replace(/-/g, '.'); // 替换开始时间的格式
                    const endDate = res.data.data.evaluateStep[i].endDate.replace(/-/g, '.'); // 替换结束时间的格式

                    items.value.push({
                        title: res.data.data.evaluateStep[i].description,
                        description: `${startDate}-${endDate}` // 拼接新的描述字段
                    });

                    if(res.data.data.evaluateStep[i].enable === 1){
                        current.value = i;
                    }
                }
            } else {
                console.log('获取失败！', res.data.msg);
            }
        })
        .catch(err => {
            console.log('获取失败！', err);
        });
};

// 获取卷首信息
const reportTitle = ref('');
const getHeadnote = () => {
    getHeadnoteApi().then(res => {
        if(res.data.code === 200){
            reportTitle.value = res.data.data.title
        }else{
            message.error("获取失败！" + res.data.msg);
        }
    }).catch(err => {
        console.log('获取失败！',err);
    })
}

onMounted(() => {
    getStepTime(),
    getHeadnote()
})
</script>

